@import "./libs/bootstrap/bootstrap.scss";
@import 'templates/_variables';
@import "fonts";
@import "mixins";

body {
	font-family: "AvenirNext";
	font-weight: 400;
	font-size: 23px;
	color: $text-color;
}
section {
	padding: 40px 0;
}
.container {
	@include responsive(desktop) {
		max-width: 1200px;
	}
}

h1 {
	color:  #0066a6;
	font-size: 57px;/* Приближение из-за подстановки шрифтов */
	font-weight: 400;
	margin-top: 60px;
	margin-bottom: 60px;
	text-transform: uppercase;

}
h2 {
	color:  $primary;
	font-size: 48px;/* Приближение из-за подстановки шрифтов */
	font-weight: 400;
	text-transform: uppercase;
	margin-top: 60px;
	margin-bottom: 60px;
}
p {
	line-height: 130%;
	margin-bottom: 11px;
}
a {
	text-decoration: underline;
	color: $text-color;

	&:hover,
	&:active {
		text-decoration: none;
	}
}
.btn {
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 1px;
    word-spacing: 4px;
    padding: 8px 15px;

	&-secondary {
		background-color: $secondary;
		border-color: $secondary;
		color: #fafbfb;
	}
}
.logo {
	display: inline-block;
	margin-right: 10px;
	background-size: cover;
	background-position: top left;
	background-repeat: no-repeat;
}
.main-nav {
	@include gradient-x(#cdd8de, #add6ec);

	&-list {
		display: flex;
		padding-left: 0;
		margin-bottom: 0;
		justify-items: space-between;

		&__element {
			display: block;
			padding: 20px 10px;

			&:first-child {
				padding-left:0;
			}
			&:last-child {
				padding-right:0;
			}

			a {
				color:  $primary;
				font-size: 17px;/* Приближение из-за подстановки шрифтов */
				font-weight: 300;	
				text-transform: uppercase;
				text-decoration: none;
			}
		}
	}
}
.header {
	background: url("../images/header-bgr.jpg") 350px 0 no-repeat;
	background-size: 100% auto;
	position: relative;
	min-height: 513px;
	margin-bottom: 40px;

	.logo__header {
		width: 220px;
		height: 91px;
		background-image: url('../images/logo.png');
		margin-top: 25px;
	}

	h1 {
		padding-right: 500px;
		margin-top: 12px;
		margin-bottom: 16px;
	}

	&-info {
		margin-left:0;
		padding-right: 500px;
		padding-left:0;

		&__element {
			color:  #515254;
			font-size: 30px;/* Приближение из-за подстановки шрифтов */
			font-weight: 400;
			list-style-type: none;
			line-height: 65px;

			&:before {
				content: "";
				display: inline-block;
				width: 20px;
				height: 20px;
				background: url('../images/icons/check-circle-regular.svg') top left no-repeat;
				margin-right: 10px;
			}
		}
	}
	.btn {
		position:absolute;
		bottom: 30px;
		left: 50%;
		min-width: 265px;
		margin-left: -28px;
	}
}
.konkurs {
	padding-top: 20px;
	padding-bottom: 60px;

	.btn {
		min-width: 470px;
		margin: 60px auto 20px;
		display: block;
		padding: 18px;
		font-size: 23px;
		letter-spacing: 0.4px;
		font-weight: 700;
		}
	h2 {
		margin-bottom:20px;
		margin-top: 20px;
	}
}
.bonus {
	@include gradient-x(#cdd8de, #add6ec);
	padding: 55px 0 45px;

	&-list {
		display: flex;
		justify-content: space-between;
		padding: 0;

		&__element {
			color: $primary;
			display: block;
			text-align: center;
			flex: 25% 0 0;
			padding: 0 10px;

			&__img {
				@include gradient-directional(#48c3f0, #005d8c);
				width: 220px;
				height:220px;
				border-radius: 110px;
				line-height: 210px;
				margin: 0 auto 28px;

				&:before {
					content: "";
					width:50%;
					height: 50%;
					line-height: initial;
					vertical-align: middle;
					display: inline-block;
					background-size: cover;
					background-repeat: no-repeat;
					background-position: center center;
				}
			}
			@for $i from 1 through 4 {
				&:nth-child(#{$i}) {
					.bonus-list__element__img {
						&:before {
							background-image: url('../images/icons/bonus'+$i+'.svg');
						}
					}
				}
			} 
			&__text {
				font-weight: 600;
				font-size: 25px;
				line-height: 118%;
			}
		}
	}
}
.conditions {
	position: relative;
	height:463px;
	padding-left: 450px;
	padding-top: 40px;

	&:before {
		content: "";
		display: block;
		position: absolute;
		z-index: -1;
		width: 600px;
		height: 100%;
		top:0;
		left:0;
		background: url("../images/condition-bgr.jpg") -70px -1px no-repeat;
		background-size: 100% auto;
	}
	h2 {
		text-align: right;
		margin-top: 0;
		margin-bottom: 30px;
	}
	.comment {
		@include gradient-x(#cdd8de, #add6ec);
		max-width: 1145px;
		width: 60%;
		margin-right: 0;
		margin-left: auto;
		color: $primary;
		text-align: right;
		position: absolute;
		bottom: 0;
		right: 0;
		font-size: 20px;
		font-weight: 600;
		padding: 16px 15px 16px 100px;
		overflow: hidden;
		height: 100px;

		&-inner {
			width: 90%;
			position: absolute;
			left: 10%;
			height: 100%;
			padding-right: 15px;
		}

		&:before {
			content:"";
			width: 73px;
			height: 100%;
			position: absolute;
			top:0;
			left:0;
			z-index:1;
			background: url('../images/triangle.png') 0 0 no-repeat;
// 			border: 60px solid transparent;
// 			border-left: 100px solid white;
// 			border-top: 100px solid white;
		}

		a {
			color: inherit;
			text-decoration: none;

			&:hover,
			a:active {
				text-decoration: underline;
			}
		}
	}
	.nav-tabs_conditions {
		border-bottom: none;
// 		justify-content: stretch;
// 		flex-wrap: nowrap;
		display:block;
		text-align: right;

		&__item {
			display:inline-block;
			//width: auto;
			margin: 0;

			&__link {
				text-decoration: none;
				font-size: 28px;
				border: none;
				border-bottom: 6px solid $text-color;
				background: none;
				display:inline-block;

				&.active {
					color: $secondary;
					border-bottom: 6px solid $secondary;
					background: none;
				}
			}
			&:first-child {
				.nav-tabs_conditions__item__link {
					padding: 8px 8px 8px 0;
					margin-right: -6px;
				}
			}
			&:last-child {
				.nav-tabs_conditions__item__link {
					padding: 8px 0 8px 8px;
				}
			}
		}
	}
	.tab-content {
		margin-top: 15px;
		text-align: right;
		line-height: 130%;
	}
}
.stages {
	padding-top:10px;
	padding-bottom: 32px;
	@include gradient-x(#cdd8de, #add6ec);

	h2 {
		margin-bottom:40px;
	}

	&-list {
		padding-left:0;
		display: flex;
		

		&__element {
			display: block;
			position: relative;

			&:before {
				content: "";
				display: block;
				position: absolute;
				height: 20px;
				top: 50px;
				width: 100%;

			}

			&:nth-child(1) {
				width: 50%;

				&:before {
					@include gradient-x(#00aeef, #005e8d);
				}

			}
			&:nth-child(2) {
				width: 25%;

				&:before {
					@include gradient-x(#f99c1c, #f15922);
				}

				.stages-list__element__period-start {
					color: $secondary;
					transform: translateX(-50%);
					display: inline-block;

				}
			}
			&:nth-child(3) {
				width: 25%;

				&:before {
					@include gradient-x(#00aeef, #005e8d);
				}

				.stages-list__element__period-start {
					transform: translateX(-50%);
					display: inline-block;
				}

				.stages-list__element__period-end {
					color: $secondary;
					transform: none;
				}
			}


			&__period-start {
				color:  $primary;
				font-size: 30px;/* Приближение из-за подстановки шрифтов */
				text-transform: uppercase;
				//width: auto;
			}
			&__period-end {
				@extend .stages-list__element__period-start;

			}
			&__description {
				max-width: 240px;
				margin: 70px auto 0;
				font-size: 21px;
			}

		}
	}
}
.nominations {
	padding-top: 0;
	padding-bottom: 0;
	
	&-list {
// 		display: flex;
// 		flex-wrap: wrap;
		justify-content: space-between;
		padding-left:0;
		margin-bottom:0;

		&__element {
			text-align: center;
			display: block;
			flex: 310px 0 0;
			min-width: 310px;
			background-size: 275px auto;
			background-repeat: no-repeat;
			background-position: top center;
			padding-top: 293px;
			margin-bottom: 45px;
			
			@for $i from 1 through 6 {
				&:nth-child(#{$i})	{
					background-image: url('../images/nomination'+$i+'.jpg');
				}
			}
			

			&__header {
				color: $secondary;
				text-transform: uppercase;
				font-size: 25px;
				margin-bottom: 3px;
				line-height: 100%;
			}
			&__description {
				font-size: 18px;
				color: #58595b;
				line-height: 130%;
			}
			&__link {
				display: block;
				text-decoration: none;
			}
		}
	}
}
.jury {
	background: url('../images/jury-bgr.jpg') top center no-repeat;
	background-size: cover;

	h2 {
		margin-top:10px;
		margin-bottom: 50px;
	}

	&-list {
		display: flex;
		justify-content: space-around;
		padding-left: 0;
		margin-bottom: 0;

		&__element {
			display: block;
			text-align: center;
			flex: 33% 0 0;


			&__img {
				width: 250px;
				height: 250px;
				border-radius: 125px;
			}
			&__name {
				font-weight: 600;
				margin: 20px auto;
				max-width: 250px;
				line-height: 130%;
				min-height: 60px;
			}
			&__position {
				font-size: 15px;
				font-weight: 300;
				max-width: 250px;
				margin: 34px auto 0;
				line-height: 130%;
			}
		}

	}
}
.partners {
	&-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-left: 0;

		&__element {
			display: block;
			flex: 10% 0 0;
			min-width: 10%;
		}

		img {
			max-width: 100%;
			height: auto;
		}
	}
}
.contact {
	text-align: center;
	padding: 130px 0;

	.btn {
		min-width: 470px;
		padding: 18px;
		font-size: 23px;
		font-weight: 600;
	}
}
.footer {
	background-color: #969696;
	color: white;
	padding: 36px 0;

	

	&-nav {
		&-list {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding-left:0;

			&__element {
				display: block;

				.logo__footer {
					display: block;
					width: 194px;
					height: 55px;
					background-image: url('../images/logo-white.png');
					background-size: 100% auto;
				}

				&__link {
					text-decoration: none;
					color: white;
					font-size: 16px;
					text-transform: uppercase;
				}
			}
		}
	}
}